@mixin retina {
  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    @content;
  }
}

// Desktop first

@mixin desktop-width-only {
  @media (min-width: $vp-1024) {
    @content;
  }
}

@mixin vp-1920 {
  @media (max-width: $vp-1920) {
    @content;
  }
}

@mixin vp-1919 {
  @media (max-width: ($vp-1920 - 1)) {
    @content;
  }
}

@mixin vp-1023 {
  @media (max-width: $vp-1023) {
    @content;
  }
}

@mixin vp-768 {
  @media (max-width: ($vp-768 - 1)) {
    @content;
  }
}

@mixin vp-320 {
  @media (max-width: $vp-320) {
    @content;
  }
}
